.select.is-active .select__text:after {
  -ms-transform: rotate(-180deg);
  -webkit-transform: rotate(-180deg);
  transform: rotate(-180deg);
}
.select.is-active .select__list {
  border-radius: 0 0 10px 10px;
  visibility: visible;
  opacity: 1;
}
.select select {
  opacity: 0;
  position: absolute;
  top: 0;
  visibility: hidden;
  left: 0;
}
.select__text {
  position: relative;
  display: block;
  padding: 6px 30px 5px 13px;
  line-height: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 29px;
  background-color: rgba(255, 255, 255, 0.25);
  border: 2px solid #363636;
  border-radius: 10px;
  transition: border-radius 300ms;
  font-family: "PFDinDisplayPro Bold", Arial, sans-serif;
  color: #181818;
}
.select__text:after {
  content: "";
