  top: 9px;
  right: 8px;
  font-size: 0.9rem;
  color: #272727;
  z-index: 10;
  font-family: "svgfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
