.form-checkbox {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0 0 0 25px;
  cursor: pointer;
  margin: 0 13px 13px 0;
}
.form-checkbox input {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}
.form-checkbox input:checked + span:after {
  top: 5px;
  visibility: visible;
  opacity: 1;
}
.form-checkbox input:disabled + span:before {
  border-color: #c7c7c7;
}
.form-checkbox input:disabled + span:after {
  color: #c7c7c7;
}
.form-checkbox span {
  font-size: 1.4rem;
  line-height: 1.2;
  font-family: "PFDinDisplayPro Regular", Arial, sans-serif;
  color: #333;
  display: inline-block;
}
.form-checkbox span:before, .form-checkbox span:after {
  position: absolute;
}
.form-checkbox span:before {
  content: "";
  top: 0;
  left: 0;
  border: 2px solid #333;
  width: 16px;
  height: 16px;
  border-radius: 2px;
}
.form-checkbox span:after {
  top: 0;
  left: 4px;
  font-size: 0.6rem;
  content: "";
  font-family: "svgfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s, visibility 0.2s, top 0.2s;
}

.form-radio {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0 0 0 25px;
  cursor: pointer;
  margin: 0 13px 13px 0;
}
.form-radio input {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}
.form-radio input:checked + span:after {
  -ms-transform: scale(1);
  -webkit-transform: scale(1);
  transform: scale(1);
}
.form-radio input:disabled + span:before {
  border-color: #c7c7c7;
}
.form-radio input:disabled + span:after {
  background: #c7c7c7;
}
.form-radio span {
  font-size: 1.4rem;
  line-height: 1.2;
  font-family: "PFDinDisplayPro Regular", Arial, sans-serif;
  color: #333;
}
.form-radio span:before, .form-radio span:after {
  position: absolute;
  border-radius: 50%;
}
.form-radio span:before {
  content: "";
  top: 0;
  left: 0;
  border: 2px solid #333;
  width: 16px;
  height: 16px;
}
.form-radio span:after {
  content: "";
  top: 5px;
  left: 5px;
  width: 6px;
  height: 6px;
  background: #197fba;
  -ms-transform: scale(0);
  -webkit-transform: scale(0);
  transform: scale(0);
  transition: transform 0.2s;
}

.captcha {
